@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
header {
  background: var(--navbar-color);
  transition: background var(--transition-delay);
  width: 100%;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.8);
  font-family: 'Roboto', sans-serif;
  position: fixed;
  z-index: 99;
  top: 0;
}

nav {
  height: 64px;
  margin: auto;
  margin-left: 5%;
  width: 95%;
  display: flex;
  align-self: center;
  justify-content: space-between;
}

nav .nav-btn-group {
  display: flex;
}

nav .nav-btn {
  height: 100%;
  width: 64px;
  display: flex;
  background: rgba(0, 0, 0, 0);
  transition: background var(--transition-delay);
  border: none;
  transition: font-size 0.5s ease-in;
  text-align: center;
  border-radius: 0px;
}

nav .nav-btn:hover {
  background: var(--nav-btn-hover-color);
  transition: background var(--transition-delay);
}

nav .nav-btn h6 {
  margin: auto;
  font-size: 12px;
  color: var(--navbar-text-color);
  transition: color var(--transition-delay);
  font-weight: 500;
}

nav h1 {
  font-size: 20px;
  color: var(--navbar-text-color);
  transition: color var(--transition-delay);
  font-weight: 400;
  margin-top: auto;
  margin-bottom: auto;
  transition: font-size 0.5s;
}

nav .vertical-bar {
  margin: auto;
  height: 40%;
  width: 1px;
  background: var(--navbar-divider-color);
  transition: background var(--transition-delay);
}

@media screen and (min-width: 768px) {
  nav h1 {
    font-size: 22px;
  }

  nav .nav-btn h6 {
    font-size: 16px;
  }

  nav .nav-btn {
    width: 120px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: 'Roboto', sans-serif;
}

.content {
  margin: auto;
  top: 64px;
  position: relative;
  width: 75%;
  min-height: 83vh;
}

main {
  width: 100%;
  margin-top: 6vh;
}

@media screen and (max-width: 768px) {
  .content {
    width: 100%;
    transition: all 0.5s;
  }

  main {
    margin-top: 0;
    transition: all 0.5s;
  }
}

.icon-only-div {
  display: flex;
  width: 100%;
  height: 100%;
  margin-top: 6vh;
  flex-direction: column;
}

.middle-text {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  color: var(--main-font-color);
  opacity: 0.75;
}

.middle-icon {
  color: var(--main-font-color);
  margin: auto;
  width: 100px;
  height: 100px;
  margin-top: 6vh;
  opacity: 0.75;
}

.svg-inline--fa.fa-w-16 {
  width: 100px;
}

footer {
  width: 100%;
  height: 48px;
  display: flex;
  flex-direction: column;
  margin-top: 96px;
  background: var(--footer-background);
  transition: background var(--transition-delay);
}

.horizontal-line {
  height: 1px;
  width: 100%;
  background: var(--footer-horizontal-divider-color);
  transition: background var(--transition-delay);
}

.footer-inner-container {
  margin: auto;
  height: 47px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 70%;
}

.vertical-line {
  margin: auto;
  height: 40%;
  background: var(--footer-vertical-divider-color);
  transition: background var(--transition-delay);
  width: 1px;
}

.footer-inner-container h4 {
  width: 50%;
  margin: auto;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  color: var(--footer-font-color);
  transition: color var(--transition-delay);
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  footer {
    margin-top: 64px;
  }
}

.title-box-container {
  position: relative;
  top: 0;
  display: flex;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.9);
  height: 30vh;
  margin: auto;
  border-radius: 15px;
}

.title-box-post-title {
  color: white;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 40px;
  font-family: 'Roboto', sans-serif;
  font-size: 36px;
  font-weight: 300;
  text-shadow: 0 0 5px rgb(47, 36, 75);
  transition: font-size 0.5s;
}

@media screen and (max-width: 768px) {
  .title-box-container {
    top: 0px;
    border-radius: 0px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
  }

  .title-box-post-title {
    font-size: 28px;
  }
}

.post-item-container {
  font-family: 'Roboto', sans-serif;
  display: flex;
  min-height: 160px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}

.post-item-container img {
  height: 160px;
  width: 96px;
  min-width: 96px;
  object-fit: cover;
  border-radius: 4px 0 0 4px;
}

.post-item-container .right-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-left: 16px;
}

.post-title {
  margin: 0;
  margin-top: -8px;
  font-weight: 400;
  text-align: left;
  font-size: min(1.5vw, 22px);
}

.post-date {
  margin: 0;
  margin-top: 4px;
  font-size: min(1vw, 14px);
  font-weight: 400;
  text-align: left;
}

.post-desc {
  margin: 0;
  margin-top: 8px;
  font-size: min(1.05vw, 16px);
  text-align: left;
}

@media screen and (max-width: 900px) {
  .post-title {
    font-size: 1.6vw;
  }

  .post-date {
    font-size: 1.4vw;
  }

  .post-desc {
    font-size: 1.2vw;
  }
}

@media screen and (max-width: 768px) {
  .post-item-container {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0;
  }

  .post-title {
    width: 100%;
    font-size: 14px;
  }

  .post-date {
    width: 100%;
    font-size: 10px;
  }

  .post-desc {
    width: 100%;
    font-size: 12px;
  }
}

.loading-animation {
  animation-name: rotate-animation;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes rotate-animation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.list-content {
  display: grid;
  grid-template-rows: repeat(3, 160px);
  grid-template-columns: repeat(2, 49%);
  grid-gap: 24px 2%;
  gap: 24px 2%;
  margin: auto;
  margin-top: 6vh;
}

@media screen and (max-width: 768px) {
  .list-content {
    grid-template-rows: 160px;
    grid-template-columns: 100%;
    grid-gap: 0 0;
    gap: 0 0;
    margin: 0;
    transition: all 0.5s;
  }
}

.post-content-container {
  background: var(--card-background);
  border: 1px solid rgba(0, 0, 0, 0.2);
  transition: background var(--transition-delay);
  border-radius: 10px;
  margin-top: 6vh;
  display: flex;
  min-height: 83vh;
}

.post-content {
  width: 90%;
  margin: auto;
  margin-top: 8px;
  color: var(--main-font-color);
  transition: color var(--transition-delay);
}

.post-content img {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.post-content-title {
  font-size: 28px;
  font-weight: 300;
  margin-bottom: 0;
}

.post-content-info {
  margin-top: 8px;
  font-weight: 500;
}

.not-highlighted {
  opacity: 0.8;
  font-weight: 400;
}

@media screen and (max-width: 768px) {
  .post-content-container {
    margin-top: 0;
    border: none;
    border-radius: 0;
  }
}

:root {
  --transition-delay: 300ms;
}

.dark-mode {
  --main-bg-color: #121212;
  --navbar-color: #1f1f1f;
  --navbar-text-color: #bb86fc;
  --navbar-divider-color: #bb86fc;
  --nav-btn-hover-color: #2e2e2e;
  --card-background: #1d1d1d;
  --main-font-color: #ffffff;
  --footer-font-color: var(--navbar-text-color);
  --footer-background: var(--navbar-color);
  --footer-horizontal-divider-color: #2e2e2e;
  --footer-vertical-divider-color: var(--navbar-divider-color);
  --link-color: var(--navbar-text-color);
}

.light-mode {
  --main-bg-color: rgba(245, 246, 250, 255);
  --navbar-color: #6200ee;
  --navbar-text-color: #ffffff;
  --navbar-divider-color: #3700b3;
  --nav-btn-hover-color: #3700b3;
  --card-background: #ffffff;
  --main-font-color: #000000;
  --footer-font-color: rgba(0, 0, 0, 0.6);
  --footer-background: var(--card-background);
  --footer-horizontal-divider-color: rgba(0, 0, 0, 0.3);
  --footer-vertical-divider-color: rgba(0, 0, 0, 0.3);
  --link-color: default;
}

body {
  margin: 0;
  background: var(--main-bg-color);
  transition: background 300ms;
  transition: background var(--transition-delay);
}

a {
  color: var(--link-color);
  transition: color 300ms;
  transition: color var(--transition-delay);
}

